.wrapper {
    padding: 0 10%;
    position: relative;
    z-index: 5;
    height: 100vh;
}
@media (max-width: 768px) {
    .wrapper {
        padding: 0 15px;
    }
}
.upperCloud {
    background-image: url('../../assets/main/mainbg.png');
    position: absolute;
    top: 0;
    left: 0;
    height: 45%;
    width: 8290px;
    z-index: 0;
    background-repeat: repeat-x;
    background-size: 15%;
    -webkit-animation: movesky 900s linear infinite;
    animation: movesky 900s linear infinite;
    overflow: hidden;
}
.uuderCloud {
    background-image: url('../../assets/mail/mainbg2.png');
    position: absolute;
    bottom: 0;
    left: 0;
    height: 40%;
    width: 11290px;
    z-index: 0;
    background-repeat: repeat-x;
    background-size: contain;
    -webkit-animation: movesky 900s linear infinite;
    animation: movesky 900s linear infinite;
    animation-direction: reverse;
    overflow: hidden;
}
@-webkit-keyframes movesky {
    from {
        transform: translate(0);
    }

    to {
        transform: translate(-5645px);
    }
}

@keyframes movesky {
    from {
        transform: translate(0);
    }

    to {
        transform: translate(-5645px);
    }
}

.code_container {
    position: absolute;
    z-index: 15;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    backdrop-filter: blur(10px);
    display: flex;
    justify-content: center;
    align-items: center;
}

.code_card {
    width: auto;
    height: auto;
    padding: 20px 0;
    margin: 0 30px;
    background: rgba(20, 102, 255, 0.63);
    border-radius: 20px;
    text-align: center;
}
@media (max-width: 768px) {
    .code_card {
        padding: 15px 20px;
        margin: 0 15px;
        width: auto;
        height: auto;
    }
}
.card_title {
    padding: 20px 60px 10px 60px;
    font-size: 25px;
    line-height: 29px;
    color: #FFFFFF;
    }
@media (max-width: 768px) {
        .card_title {
            padding: 10px;
        }
    }
.card_text {
    padding: 0 100px;
    font-size: 18px;
    line-height: 20px;
    color: #76B9FF;
}
@media (max-width: 768px) {
    .card_text {
        padding: 10px;
    }
}