.wrapper {
    padding: 0 10%;
    position: relative;
    z-index: 5;
    min-height: 100vh;
}
@media (max-width: 768px) {
    .wrapper {
        padding: 0 15px;
    }
}


.upperCloud {
    background-image: url('../../assets/main/mainbg.png');
    position: absolute;
    top: 0;
    left: 0;
    height: 95%;
    width: 8290px;
    z-index: 0;
    background-repeat: repeat-x;
    background-size: contain;
    -webkit-animation: movesky 900s linear infinite;
    animation: movesky 900s linear infinite;
    overflow: hidden;
}

.uuderCloud {
    background-image: url('../../assets/mail/mainbg2.png');
    position: absolute;
    bottom: 0;
    left: 0;
    height: 25%;
    width: 11290px;
    z-index: 0;
    background-repeat: repeat-x;
    background-size: contain;
    -webkit-animation: movesky 900s linear infinite;
    animation: movesky 900s linear infinite;
    animation-direction: reverse;
    overflow: hidden;
}

@-webkit-keyframes movesky {
    from {
        transform: translate(500px);
    }

    to {
        transform: translate(-5645px);
    }
}

@keyframes movesky {
    from {
        transform: translate(-500px);
    }

    to {
        transform: translate(-5645px);
    }
}

.container {
    padding-top: 7%;
    position: relative;
    z-index: 3;
    height: 100%;
    display: flex;
    flex-direction: column;
}



.content {
    padding-top: 3%;
    display: flex;
    height: 100%;
    background-image: url(../../assets/main/bird5.png);
    background-position: 20% 0;
    background-repeat: no-repeat;
    background-size: 40%;
}
@media (max-width: 768px) {
    .content {
        flex-direction: column;
        align-items: center;
        background-image: url(../../assets/main/bird5.png);
        background-position: 50% 7%;
        background-repeat: no-repeat;
        background-size: 80%;
        margin-bottom: 15px;
    }
}
.birds {
    flex: 1 0 55%;
}
@media (max-width: 768px) {
    .birds {
        margin-bottom: 50%;
    }
}
.formForgot {
    padding: 0% 10% 10% 0;
    flex: 1 1 50%;
    display: flex;
    flex-direction: column;
    color: #0047CA;
}
@media (max-width: 768px) {
    .formForgot {
        padding: 0;
        min-width: 290px;
        width: 100%;
        max-width: 530px;
    }
}
.formTittle {
    color: #1466FF;
    font-weight: 600;
    font-size: 40px;
    line-height: 47px;
    text-transform: capitalize;

}

.formText {
    padding-top: 35px;
    font-weight: 400;
    font-size: 20px;
    line-height: 23px;
    text-transform: lowercase;
    color: #0047CA;
}

.form {
    padding: 5% 40% 0 0;

    label {
        font-weight: 400;
        font-size: 17px;
        line-height: 20px;
        font-variant: small-caps;
    }
    input {
        width: 100%;
        background: #76B9FF;
        border-top: none;
        border-right: none;
        border-left: none;
        border-bottom: 4px solid #1466FF;
        background-color: transparent;
        margin-bottom: 45px;
        font-weight: 500;
        font-size: 20px;
        line-height: 20px;
        color: #1466FF;
        &:focus-visible {
            outline: none;
        }
    }
    input:-webkit-autofill {
        -webkit-text-fill-color: #1466FF !important;
        transition: all 5000s ease-in-out;
    }
    .email {
        padding-top: 30px;
    }
}
@media (max-width:768px) {
    .form {
        padding: 10px 0 0 0;
    }
}
.signBtn {
    min-width: 100%;
    padding: 23px 75px;
    background: #1466FF;
    box-shadow: 0px 3px 10px rgba(7, 62, 164, 0.53);
    border-radius: 24px;
    font-weight: 600;
    font-size: 20px;
    line-height: 23px;
    font-variant: small-caps;
    color: #DAEDFF;
    &:hover {
        background: #185FE3;
        box-shadow: 0px 3px 10px rgba(7, 62, 164, 0.53);
    }
    &:active {
        background: #0C52D5;
        box-shadow: inset 2px 7px 10px rgba(0, 0, 0, 0.25);
    }
    &:disabled {
        background: #5C5C5C;
        box-shadow: none;
    }
}
